.container {
  padding: 0 20px;
  overflow: hidden;

  @media (min-width: $tablet-width) {
    padding: 0 30px;
  }

  @media (min-width: $tablet-desktop-width) {
    padding: 0 60px;
  }

  @media (min-width: $desktop-width) {
    max-width: 1440px;
    margin: 0 auto;
    padding: 0 110px;

    overflow: visible;
  }
}
